@import '_fonts';


:root {
  --body-font: Whitney, Helvetica, Arial, sans-serif;
  --primary-color: #000;
  --secondary-color: #fff;
}

[data-theme="dark"] {
  --primary-color: #EC4D37;
  --secondary-color: #1D1B1B;
  --font-color: #e1e1ff;
  --bg-color: #161625;
  --heading-color: #818cab;
}

[data-theme="light"] {
  --primary-color: #2F3C7E;
  --secondary-color: #FBEAEB;
  --font-color: #424242;
  --bg-color: #fff;
  --heading-color: #292922;
}

[data-theme="one"] {
  --primary-color: #F0A07C;
  --secondary-color: #4A274F;
  --font-color: #424242;
  --bg-color: #fff;
  --heading-color: #292922;
}

[data-theme="two"] {
  --primary-color: #F4A950;
  --secondary-color: #161B21;
  --font-color: #424242;
  --bg-color: #fff;
  --heading-color: #292922;
}

[data-theme="three"] {
  --primary-color: #FFE67C;
  --secondary-color: #303f9f;
  --font-color: #424242;
  --bg-color: #fff;
  --heading-color: #292922;
}

[data-theme="four"] {
  --primary-color: #fff;
  --secondary-color: #485564;
  --font-color: #424242;
  --bg-color: #fff;
  --heading-color: #292922;
}

@keyframes bounce {
  0%,
    20%,
    50%,
    80%,
    100% {
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  40% {
    -moz-transform: translateY(-20px);
    -ms-transform: translateY(-20px);
    -webkit-transform: translateY(-20px);
    transform: translateY(-20px);
  }

  60% {
    -moz-transform: translateY(-15px);
    -ms-transform: translateY(-15px);
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px);
  }
}


@keyframes fadeInDown {
  0% {
    opacity: 0;
    -moz-transform: translate3d(0, -100%, 0);
    -ms-transform: translate3d(0, -100%, 0);
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  100% {
    opacity: 1;
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}


.fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
  animation: fadeInDown;
  animation-duration: 1.2s;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -moz-transform: translate3d(0, 100%, 0);
    -ms-transform: translate3d(0, 100%, 0);
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }

  100% {
    opacity: 1;
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}


.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
  animation: fadeInUp;
  animation-duration: 1.2s;
}

@keyframes zoomIn {
  0% {
    opacity: 0;
    -moz-transform: scale3d(0.3, 0.3, 0.3);
    -ms-transform: scale3d(0.3, 0.3, 0.3);
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }

  50% {
    opacity: 1;
  }
}


.zoomIn {
  -webkit-animation-name: zoomIn;
  animation-name: zoomIn;
  animation: zoomIn;
  animation-duration: 1.2s;
}

.icon-mail:before, .icon-linkedin:before, .icon-github:before, .icon-medium:before, .icon-codepen:before {
  animation-name: zoomIn;
  animation: zoomIn;
  animation-duration: 1.2s;
}

#body-container .grid-item {
  &.reveal {
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
  -webkit-transition: all 320ms ease;
  -moz-transition: all 320ms ease;
  -ms-transition: all 320ms ease;
  -o-transition: all 320ms ease;
  transition: all 320ms ease;
  -webkit-transform: translateY(100px);
  -moz-transform: translateY(100px);
  -ms-transform: translateY(100px);
  -o-transform: translateY(100px);
  transform: translateY(100px);
  opacity: 0;
}

*, :after, :before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-size: 1em;
  color: var(--primary-color, #000);
  background-color: var(--secondary-color, #fff);
}

::-webkit-scrollbar {
  display: none;
}

.no-scroll {
  overflow-y: hidden;
}

.mobile_menu.scrollUp {
  transform: translateY(100px);
  transition: 400ms all ease-in;
}

header {
  position: fixed;
  width: 100%;
}

#head {
  display: flex;
  padding: 16px 16px 0 0;
  justify-content: space-between;
  width: 100%;
}

.colorme, .icon-down {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 1em;
  cursor: pointer;
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
  animation-delay: 5000ms;
}

.icon-magic {
  display: inline-block;
  font-size: 1.5em;
}

.colorme em {
  display: inline-block;
  font-size: 0.8em;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
}

.shape {
  stroke-dasharray: 75 540;
  stroke-dashoffset: -193;
  stroke-width: 5px;
  fill: transparent;
  stroke: #00b7c7;
  border-bottom: 5px solid black;
  transition: stroke-width 1s, stroke-dashoffset 1s, stroke-dasharray 1s;
}

.resume-text {
  text-align: center;
  font-size: 1em;
  letter-spacing: 2px;
  color: var(--primary-color, #000);
  top: -48px;
  position: relative;
}

.resume-wrapper:hover .shape, #show-projects:hover .shape {
  stroke-width: 2px;
  stroke-dashoffset: 0;
  stroke-dasharray: 760;
}

/*fonts*/

a {
  text-decoration: none;
  color: inherit;
  &:hover {
    color: #00b7c7 !important;
  }
}

p {
  word-spacing: 0.01em;
  margin: 0 0 10px 0;
  font-family: Whitney, Helvetica, Arial, sans-serif !important;
}

li, input, textarea, button {
  font-family: Whitney, Helvetica, Arial, sans-serif !important;
}

h1 {
  margin: 0 !important;
  font-family: ChronicleDisplay, Helvetica, Arial, sans-serif;
}

h2, h5 {
  font-family: Gotham, Helvetica, Arial, sans-serif;
}

#intro-content .divider {
  opacity: 1;
  margin: 30px auto;
  text-align: center;
}

.divider {
  display: block;
  width: 0px;
  opacity: 0;
  margin: 30px 0;
  height: 2px;
  background-color: #00b7c7;
  transition: all 1000ms;
}

.line {
  opacity: 1;
  width: 100px;
}

::selection {
  background: #03d4bf;
  color: #fff;
}

.white {
  color: var(--secondary-color, #fff) !important;
  background: var(--primary-color, #000);
  border-radius: 15px;
}

#mobile_menu {
  display: none;
}

#nav-menu {
  position: fixed;
  top: 50%;
  bottom: auto;
  right: 50px;
  transform: translateY(-50%);
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity .5s ease;
  &.active {
    visibility: visible;
    opacity: 1;
    transition: visibility 0s, opacity .5s ease;
  }
}

.nav-container li {
  list-style-type: none;
  text-align: right;
  .dot {
    position: relative;
    top: 7px;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.25);
    transition: transform .2s, background-color .5s;
    transform-origin: 50% 50%;
  }
}

#dot-nav a {
  &:focus .dot-label, &:hover .dot-label {
    opacity: 1;
  }
}

#nav-menu a {
  &:hover span {
    transform: scale(0.75);
  }
  span {
    display: inline-block;
    transform: scale(0.6);
  }
  &.is-selected .dot {
    background-color: #00b7c7;
  }
  &:focus .dot-label, &:hover .dot-label {
    opacity: 1;
  }
}

.nav-container li .dot-label {
  color: var(--primary-color, #000);
  position: relative;
  top: 3px;
  padding: .4em .5em;
  font-family: Gotham, Helvetica, Arial, sans-serif;
  font-size: 1em;
  font-weight: 500;
  text-transform: uppercase;
  opacity: 0;
  transition: transform .2s, opacity .2s;
  transform-origin: 100% 50%;
}

#intro-section {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100vh;
  flex-direction: column;
}

.section {
  min-height: 700px;
  width: 100%;
}

#intro-content {
  text-align: center;
  width: 100%;
  .heading {
    font-size: 4.5em;
    margin: 0;
    font-weight: 700;
  }
  .subtext {
    display: inline-block;
    padding: 1.2em 0;
    width: 50%;
  }
}

.content-wrapper {
  padding: 50px 150px;
}

.social-icons {
  margin: 20px 0;
  a {
    color: var(--primary-color, #000);
    margin: 0 8px;
  }
  .list-group-item {
    font-size: 2.2em;
  }
}

.icon-github-link, .icon-folder {
  font-size: 2.2em;
}

.projects__ProjectLinks a {
  margin: 0 10px;
}

.icon-link {
  font-size: 1.9em;
}

.heading {
  font-size: 3em;
}

#about-section {
  p {
    font-size: 1.2em;
  }
  ul {
  font-size: 1.2em;
  list-style: none;
  margin: 15px 0 20px 20px;
  }
  li {
    margin-bottom: 5px;
  }
}

#skills-section.section {
  display: block;
  height: auto;
}

.skills-flex-wrapper {
  display: flex;
  height: 30em;
  flex-flow: column wrap;
  width: 100%;
  ul {
    margin-top: 25px;
  }
  .list {
    margin-top: 30px;
    margin-left: 20px;
    list-style-type: none;
  }
}

.dev-section {
  width: 50%;
  max-width: 450px;
  padding: 20px;
}

.frameworks-section, .tools-section {
  width: 50%;
  padding: 20px;
  min-height: 200px;
}

.dev-container {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.dev-item span {
  font-size: 12px;
  text-align: center;
  z-index: 10;
  left: 20px;
  top: 13px;
  display: block;
}

.tools-container li {
  font-weight: bold;
  padding: 5px 20px;
  margin: 10px;
  display: inline-block;
  border-radius: 15px;
  color: var(--secondary-color, #fff);
  background-color: var(--primary-color, #000);
}

.icon-check {
  margin-right: 8px;
}

/*------------------Shapes*/

.dev-item {
  display: flex;
  position: relative;
  width: 80px;
  height: 46.19px;
  margin: 32.09px 12px;
  border-left: solid 2px var(--primary-color, #000);
  border-right: solid 2px var(--primary-color, #000);
  justify-content: space-evenly;
  align-items: center;
  &:before, &:after {
    content: "";
    position: absolute;
    z-index: 1;
    width: 56.57px;
    height: 56.57px;
    -webkit-transform: scaleY(0.5774) rotate(-45deg);
    -ms-transform: scaleY(0.5774) rotate(-45deg);
    transform: scaleY(0.5774) rotate(-45deg);
    background-color: inherit;
    left: 9.7157px;
  }
  &:before {
    top: -28.2843px;
    border-top: solid 2.8284px var(--primary-color, #000);
    border-right: solid 2.8284px var(--primary-color, #000);
  }
  &:after {
    bottom: -28.2843px;
    border-bottom: solid 2.8284px var(--primary-color, #000);
    border-left: solid 2.8284px var(--primary-color, #000);
  }
}

/*Accordion*/

.accordion-section {
  display: flex;
  max-width: 1000px;
  margin: 100px auto 0;
  .menu {
    font-family: Gotham, Helvetica, Arial, sans-serif;
    padding-right: 35px;
    width: 40%;
    border-left: 1px solid var(--primary-color, #000);
  }
}

.accordion-content {
  width: 60%;
}

.accordion-section .menu div {
  padding: 15px 20px 15px 40px;
  cursor: pointer;
  position: relative;
  vertical-align: middle;
  font-weight: 700;
  transition: 400ms all ease-in;
  &:hover {
    background-color: var(--primary-color, #000);
    color: var(--secondary-color, #fff);
  }
  &.active {
    border-left: 2px solid var(--primary-color, #000);
  }
}

ul.nacc {
  position: relative;
  height: 0px;
  list-style: none;
  margin: 0;
  padding: 0;
  transition: 0.5s all cubic-bezier(0.075, 0.82, 0.165, 1);
  li {
    opacity: 0;
    position: absolute;
    transition: 1s all cubic-bezier(0.075, 0.82, 0.165, 1);
    h4 {
      margin: 0 0 15px 0;
    }
    &.active {
      transition-delay: .3s;
      z-index: 2;
      opacity: 1;
      transform: translateX(0px);
    }
  }
}

/*Personal Projects*/

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 30px;
}

.grid-item {
  margin: 0.5em;
  position: relative;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  -moz-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  -ms-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  -o-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  -webkit-transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -ms-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
  &:hover {
    z-index: 9;
    -webkit-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
    -moz-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
    -ms-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
    -o-box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
  }
}

.project-box {
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  height: 100%;
  color: var(--secondary-color, #fff);
  background-color: var(--primary-color, #000);
  padding: 25px;
  border-radius: 2px;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
}

.projects__ProjectHeader {
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  align-items: flex-end;
  margin-bottom: 30px;
}

.projects__ProjectName {
  font-size: 22px;
  color: var(--secondary-color, #fff);
  margin: 0px 0px 10px;
}

.projects__TechList {
  list-style-type: none;
  -webkit-box-flex: 1;
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  margin-top: 20px;
  li {
    font-family: 'Fira Code' !important;
    font-size: 0.8em;
    word-spacing: -0.2em;
    letter-spacing: 0.1em;
    line-height: 2;
    &:after {
      content: "|";
      margin: 0 10px;
      position: relative;
    }
    &:last-child:after {
      content: "";
      position: relative;
    }
  }
}

#show-projects {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  .shape {
    stroke-dasharray: 87 540;
    stroke-dashoffset: -242;
    stroke-width: 5px;
    fill: transparent;
    stroke: #00b7c7;
    border-bottom: 5px solid black;
    transition: stroke-width 1s, stroke-dashoffset 1s, stroke-dasharray 1s;
  }
}

.projects-link {
  text-align: center;
  font-size: 1em;
  letter-spacing: 2px;
  color: var(--primary-color, #000);
  position: absolute;
}

/*form section*/

.form-section {
  padding: 50px;
}

input, textarea {
  color: var(--primary-color, #fff);
  background-color: var(--secondary-color, #000);
  border-width: 0 0 1px;
  border-color: var(--primary-color, #fff);
  outline: 0;
  resize: none;
  padding-bottom: 5px;
}

input {
  width: 35%;
  display: inline-block;
  &.form-mail {
    margin-left: 1.5em;
  }
}

textarea {
  width: 80%;
  display: block;
  margin-top: 4em !important;
}

#contact-content button {
  background-color: var(--primary-color, #fff);
  color: var(--secondary-color, #000);
  border: none;
  font-weight: bold;
  cursor: pointer;
  margin-top: 4em;
  border-radius: 6px;
  padding: 1em 2em;
  transform: translateZ(0);
  transition: -webkit-transform .25s ease-out;
  transition: transform .25s ease-out;
  transition: transform .25s ease-out, -webkit-transform .25s ease-out;
  box-shadow: 2px 2px 8px 0 rgba(0, 0, 0, 0.2);
  &:hover {
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
  }
}

footer #footer {
  font-weight: bold;
  padding: 30px;
  text-align: center;
  background: linear-gradient(85deg, var(--primary-color, #fff), var(--secondary-color, #000));
  color: var(--secondary-color, #000);
}

/*hamburger menu*/

#burger-menu {
  width: 60px;
  height: 10%;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  span {
    display: block;
    position: absolute;
    height: 6px;
    width: 100%;
    background: var(--primary-color, #000);
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    -moz-transition: .25s ease-in-out;
    -o-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
    &:nth-child(1) {
      top: 0px;
    }
    &:nth-child(2), &:nth-child(3) {
      top: 14px;
    }
    &:nth-child(4) {
      top: 28px;
    }
  }
  &.open span {
    &:nth-child(1) {
      top: 18px;
      width: 0%;
      left: 50%;
    }
    &:nth-child(2) {
      -webkit-transform: rotate(45deg);
      -moz-transform: rotate(45deg);
      -o-transform: rotate(45deg);
      transform: rotate(45deg);
    }
    &:nth-child(3) {
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      -o-transform: rotate(-45deg);
      transform: rotate(-45deg);
    }
    &:nth-child(4) {
      top: 18px;
      width: 0%;
      left: 50%;
    }
  }
}

.menu_activated #burger-menu span {
  background: var(--secondary-color, #fff);
}

#burger-menu:before {
  content: "";
  width: 0;
  height: 0;
  background-color: var(--primary-color, #000);
  border-radius: 50%;
  position: absolute;
  bottom: -.5em;
  left: calc(50% - .5em);
  will-change: width, height;
  transition: transform 0.25s cubic-bezier(0.04, -0.1, 0.29, 0.98), width 0.25s cubic-bezier(0.04, -0.1, 0.29, 0.98), height 0.25s cubic-bezier(0.04, -0.1, 0.29, 0.98);
}

.menu_activated #burger-menu:before {
  width: 100vmax;
  height: 100vmax;
  transform: translate3d(-50vh, -50vh, 0) scale(5);
  transition-duration: 1s;
}

/*Responsive*/

@media screen and (max-width: 767px) and (orientation: landscape) {
  .section {
    min-height: auto;
  }
}

@media screen and (max-width: 1024px) and (orientation: landscape) {
  #intro-content .subtext {
    padding: 0.5em 0;
  }
  .mobile_menu {
    height: 20% !important;
  }
  #intro-content .heading {
    font-size: 3.5em;
  }
}

@media screen and (min-width: 1024px) {
  .mobile_menu {
    display: none !important;
  }
}

.mobile_menu {
  transition: all 0.5s;
  padding-top: 16px;
  width: 100%;
  height: 10%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background: var(--secondary-color, #fff);
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 9998;
}

.menu__nav {
  width: 100%;
  height: 0;
  transition: opacity .2s ease-out;
  opacity: 0;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  flex-direction: column;
  z-index: 2;
}

.menu__list, .mob-theme-switch-wrapper {
  width: 100%;
  max-height: 100%;
  display: none;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.menu__group {
  color: var(--secondary-color, #fff);
  padding: .3rem 3rem;
  font-size: 2.5rem;
  font-weight: 700;
  text-transform: uppercase;
}

/* activated state */

.menu_activated {
  transform: translateY(0);
  height: 100% !important;
  background: var(--secondary-color, #fff);
  .menu__nav {
    height: 100%;
    opacity: 1;
    align-items: center;
    will-change: opacity;
    transition-duration: .2s;
    transition-delay: .3s;
  }
  .menu__list {
    display: block;
  }
}

.mob-theme-switch-wrapper {
  display: block;
}

@media screen and (max-width: 1023px) {
  .arrow {
    display: none;
  }
  #intro-content {
    .heading {
      font-size: 3.5em;
    }
    .subtext {
      width: 85%;
      font-size: 0.8em;
    }
  }
  .colorme {
    color: var(--secondary-color, #fff);
    animation: none;
  }
  #burger-menu {
    width: 45px;
  }
  header {
    display: none !important;
  }
  .heading {
    font-size: 2em;
  }
  #nav-menu {
    display: none;
  }
  #mobile_menu {
    display: block;
  }
  .content-wrapper {
    padding: 25px 15px;
  }
  .skills-flex-wrapper {
    height: auto;
  }
  .dev-section, .tools-section, .tools-container, input, textarea {
    width: 100%;
  }
  .social-icons a {
    margin: 0 5px;
  }
  /*Experience Section*/
  .accordion-section {
    flex-direction: column;
    min-height: 650px;
    margin: 0;
    .menu {
      width: 100%;
      margin: 0 0 50px 0;
    }
  }
  .accordion-content {
    width: 100%;
  }
  /*Form Section*/
  input.form-mail {
    margin: 4em 0 0 0;
  }
  .form-section {
    text-align: center;
  }
  #contact-content button {
    width: 100%;
  }
}

@media screen and (min-width: 1600px) {
  .content-wrapper {
    padding: 50px 320px;
  }
}